/* General Body and App Container Styles */
body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e9ecef; /* Soft light gray background */
  color: #343a40; /* Dark charcoal text */
  line-height: 1.6;
  scroll-behavior: smooth;
}

.App {
  max-width: 1800px; /* Slightly wider main container */
  margin: 0 auto;
  padding: 50px;
}

/* Header Styles */
.App-header {
  background: linear-gradient(135deg, #2c3e50 0%, #4a69bd 100%); /* Deep blue-gray to muted blue gradient */
  padding: 60px 40px;
  color: white;
  margin-bottom: 100px;
  border-radius: 25px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3); /* More pronounced shadow */
  text-align: center;
  position: relative;
  overflow: hidden;
}

.App-header::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: rgba(255, 255, 255, 0.08); /* Slightly more visible overlay pattern */
  transform: rotate(45deg);
  z-index: 0;
}

.App-header h1 {
  font-size: 5rem; /* Larger, more impactful title */
  margin: 0;
  letter-spacing: 4px; /* Increased letter spacing */
  font-weight: 900; /* Bolder font weight */
  text-shadow: 4px 4px 8px rgba(0, 0, 0, 0.5); /* Stronger text shadow */
  position: relative;
  z-index: 1;
}

/* Main Category Section Styles */
.main-category-section {
  margin-bottom: 120px;
}

.main-category-title {
  font-size: 4.2rem; /* Slightly larger */
  text-transform: uppercase;
  color: #2c3e50;
  margin-bottom: 70px; /* Increased margin */
  position: relative;
  padding-bottom: 30px; /* Increased padding */
  text-align: center;
  font-weight: 800; /* Bolder */
  letter-spacing: 2px; /* Added letter spacing */
}

.main-category-title::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  width: 180px; /* Wider underline */
  height: 8px; /* Thicker underline */
  background-color: #007bff; /* Primary blue accent */
  border-radius: 4px;
}

/* Sub Category Section Styles (Grid Layout) */
.sub-category-section {
  margin-bottom: 80px;
  padding: 0 30px; /* Increased padding */
}

.sub-category-section h2 {
  font-size: 3.2rem; /* Slightly larger */
  text-align: left;
  margin-bottom: 50px; /* Increased margin */
  padding-left: 30px; /* Increased padding */
  border-left: 12px solid #007bff; /* Thicker border */
  color: #34495e;
  font-weight: 700; /* Bolder */
}

.sub-category-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Adjusted minmax for larger cards */
  gap: 40px; /* Increased gap */
  justify-content: center;
  overflow-x: hidden;
}

.sub-category-card {
  background-color: #ffffff;
  border-radius: 20px; /* More rounded corners */
  box-shadow: 0 12px 25px rgba(0, 0, 0, 0.15); /* Stronger shadow */
  overflow: hidden;
  transition: transform 0.4s ease, box-shadow 0.4s ease;
  display: flex;
  flex-direction: column;
}

.sub-category-card:hover {
  transform: translateY(-15px); /* More pronounced lift effect */
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25);
}

.sub-category-card h3 {
  font-size: 2.2rem; /* Slightly larger */
  padding: 30px; /* Increased padding */
  margin: 0;
  background-color: #f1f3f5; /* Slightly darker background for heading */
  color: #2c3e50;
  text-align: center;
  font-weight: 700;
  border-bottom: 1px solid #dee2e6;
}

.sub-category-content {
  padding: 25px; /* Increased padding */
  flex-grow: 1;
}

/* Model Grid Styles (within sub-category content) */
.model-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); /* Larger images */
  gap: 30px; /* Adjusted gap */
  justify-content: center;
}

.model-card {
  background-color: #ffffff;
  border-radius: 15px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  width: 280px; /* Fixed width for larger images */
  height: 280px; /* Fixed height for uniform image boxes */
  margin: 0 auto;
}

.model-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 25px rgba(0, 0, 0, 0.18);
}

.representative-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Responsive Adjustments */
@media (max-width: 1200px) {
  .App {
    padding: 40px;
  }
  .App-header h1 {
    font-size: 4rem;
  }
  .main-category-title {
    font-size: 3.5rem;
  }
  .sub-category-section h2 {
    font-size: 2.8rem;
  }
  .sub-category-container {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
  .model-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); /* Adjusted for larger images */
  }
  .model-card {
    width: 250px;
    height: 250px;
  }
}

@media (max-width: 992px) {
  .App {
    padding: 30px;
  }
  .App-header h1 {
    font-size: 3.5rem;
  }
  .main-category-title {
    font-size: 3rem;
  }
  .sub-category-section h2 {
    font-size: 2.5rem;
  }
  .sub-category-container {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
  .model-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
  .model-card {
    width: 200px;
    height: 200px;
  }
}

@media (max-width: 768px) {
  .App {
    padding: 20px;
  }
  .App-header h1 {
    font-size: 3rem;
  }
  .main-category-title {
    font-size: 2.5rem;
  }
  .sub-category-section h2 {
    font-size: 2rem;
  }
  .sub-category-container {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
  }
  .model-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
  .model-card {
    width: 150px;
    height: 150px;
  }
}

@media (max-width: 576px) {
  .App {
    padding: 15px;
  }
  .App-header h1 {
    font-size: 2.5rem;
  }
  .main-category-title {
    font-size: 2rem;
  }
  .sub-category-section h2 {
    font-size: 1.8rem;
  }
  .sub-category-container {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  .model-grid {
    grid-template-columns: 1fr;
  }
  .model-card {
    width: 150px; /* Increased size for mobile */
    height: 150px; /* Increased size for mobile */
  }
}