
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.85); /* Darker overlay */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(5px); /* Subtle blur effect */
}

.modal-content {
  background-color: #ffffff; /* Clean white background */
  padding: 25px;
  border-radius: 12px;
  position: relative;
  width: 95%;
  max-width: 1100px; /* Slightly wider modal */
  max-height: 90vh;   /* Taller modal */
  display: flex;
  flex-direction: column;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3); /* More prominent shadow */
}

.close-button {
  position: absolute;
  top: 15px;
  right: 20px;
  background: none;
  border: none;
  font-size: 3rem; /* Larger close button */
  cursor: pointer;
  color: #333; /* Darker color */
  transition: color 0.2s ease;
  z-index: 1001; /* Ensure it's above other content */
}

.close-button:hover {
  color: #e74c3c; /* Red on hover */
}

.modal-image-strip {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  gap: 25px; /* More space between images */
  padding: 20px 0;
  margin-top: 20px; /* Adjusted margin */
}

/* Custom scrollbar for Webkit browsers */
.modal-image-strip::-webkit-scrollbar {
  width: 8px;
}

.modal-image-strip::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.modal-image-strip::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

.modal-image-strip::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.modal-image-strip img {
  max-width: 100%; /* Ensure images don't overflow */
  height: auto;
  object-fit: contain;
  border-radius: 8px; /* Slightly more rounded corners */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Responsive adjustments for modal */
@media (max-width: 768px) {
  .modal-content {
    padding: 15px;
    max-width: 90%;
  }

  .close-button {
    font-size: 2.5rem;
    top: 10px;
    right: 15px;
  }

  .modal-image-strip {
    gap: 15px;
  }
}

@media (max-width: 480px) {
  .modal-content {
    padding: 10px;
    max-width: 95%;
  }

  .close-button {
    font-size: 2rem;
    top: 5px;
    right: 10px;
  }

  .modal-image-strip {
    gap: 10px;
  }
}
